@import "https://fonts.googleapis.com/css2?family=Changa+One&display=swap";
body {
  color: #fff;
  background-image: url("https://i.pinimg.com/originals/7c/83/af/7c83afe4915fc72564941ab32ff1e37d.gif");
  font-family: Changa One, sans-serif;
}

.img-resize {
  max-width: 200px;
  max-height: 200px;
}

form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

label {
  margin-top: 10px;
  margin-bottom: 5px;
}

input[type="text"], input[type="password"] {
  width: 200px;
  margin-bottom: 15px;
  padding: 5px;
}

img {
  max-width: 100%;
  height: auto;
  margin-bottom: 5px;
  display: block;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  margin-top: 20px;
  padding: 10px 20px;
}

button:hover {
  background-color: #3e8e41;
}

#images {
  display: none;
}

h1 {
  text-align: center;
  color: #fff;
  margin-top: 50px;
  font-size: 48px;
  font-weight: bold;
}

body.logged-in {
  background-color: #000;
}

#welcome-message {
  color: #fff;
  font-size: 36px;
  animation: .5s ease-in-out infinite alternate shake;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(10px);
  }
}

#status-update {
  border: none;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
  box-shadow: 1px 1px 5px #ccc;
}

#submit-status {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 1px 1px 5px #ccc;
}

#user-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 20px;
  box-shadow: 1px 1px 5px #ccc;
}

#user-section {
  height: 100vh;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

h3 {
  color: #fff;
}

#user-list {
  width: 200px;
  max-height: 60vh;
  color: #333;
  background-color: #fffc;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 12rem;
  padding: 15px;
  font-weight: 500;
  position: absolute;
  top: 80px;
  right: 20px;
  overflow-y: auto;
  box-shadow: 0 2px 6px #0000004d;
}

#users li {
  cursor: pointer;
  border-bottom: 1px solid #ccc;
  padding: 10px;
  font-family: Arial, sans-serif;
  list-style: none;
}

#users li:last-child {
  border-bottom: none;
}

#users li:hover {
  background-color: #0000001a;
}

#user-info {
  width: 400px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 15px;
  font-family: Arial, sans-serif;
  position: absolute;
  top: 20px;
  left: 240px;
  box-shadow: 0 2px 6px #0000004d;
}

#user-info h3 {
  color: #333;
  margin-top: 0;
  font-size: 18px;
}

#user-info p {
  margin: 10px 0;
  font-size: 14px;
  line-height: 1.5;
}

#h33 {
  color: #000;
}

@keyframes post-flash {
  0% {
    background-color: #3984d0;
  }

  50% {
    background-color: #56a8ff;
  }

  100% {
    background-color: #3984d0;
  }
}

@keyframes post-flash {
  0% {
    background-color: #1a3a5c;
  }

  50% {
    background-color: #3a5c89;
  }

  100% {
    background-color: #1a3a5c;
  }
}

.inloggedUserPost {
  min-width: 10%;
  background-color: #1a3a5c;
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 15px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  animation: 1s ease-out post-flash;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px #1a3a5c66;
}

.inloggedUserPost:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px #1a3a5c99;
}

.post-card {
  max-width: 80%;
  min-width: 10%;
  white-space: nowrap;
  word-wrap: break-word;
  overflow-wrap: break-word;
  word-break: break-word;
  -webkit-hyphens: auto;
  hyphens: auto;
  background-color: #3984d0;
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 15px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  display: inline-block;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 5px #d31010bd;
}

img {
  width: 100px;
}

.hidden {
  display: none;
}

#show-members {
  float: right;
  height: 70px;
  margin-bottom: 100rem;
  margin-right: 3rem;
  font-size: 20px;
  animation: 5s fadeIn;
  display: none;
}

.fade-in-text {
  font-family: Arial;
  font-size: 15px;
  animation: 5s fadeIn;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

